import trailerStars from "../../components/trailerStars.vue"
export default {
  components: {
    trailerStars
  },
  data() {
    return {
      carouseList: [],
      hotList: [],
      trailerList: [],
      guessULikeList: [],
      animationData: {},
      animationDataArr: []
    }
  },
  onUnload() {
    this.animationData = {}
  },
  onLoad() {
    this.getCarouselList()
    this.getHotList()
    this.getTrailerList()
    this.getGuessULikeList()
    

    this.animation = uni.createAnimation({
      duration: 400,
      timingFunction: "ease-out"
    })

  },
  methods: {
    praiseMe(e) {

      const gIndex = e.currentTarget.dataset.gindex
      this.animation.translateY(-60).opacity(1).step()
      this.animationData = this.animation
      this.animationDataArr[gIndex] = this.animationData.export()
      
      setTimeout(function() {
        this.animation.translateY(0).opacity(0).step({
          duration: 0,
        })
        this.animationData = this.animation
        this.animationDataArr[gIndex] = this.animationData.export()
      }.bind(this), 500);

    },
    getGuessULikeList (callback) {
      uni.request({
        url: this.serverUrl + '/index/guessULike?qq=lee83440456',
        method: "POST",
        success: (res) => {
          if (res.data.status === 200) {
            this.guessULikeList = res.data.data
          }
          if (callback) {
            callback()
          }
        }
      });
    },
    getCarouselList () {
      uni.request({
        url: this.serverUrl + '/index/carousel/list?qq=lee83440456',
        method: "POST",
        success: (res) => {
          if (res.data.status === 200) {
            this.carouseList = res.data.data
          }
        }
      });
    },
    getHotList () {
      uni.request({
        url: this.serverUrl + '/index/movie/hot?qq=lee83440456&type=superhero',
        method: "POST",
        success: (res) => {
          if (res.data.status === 200) {
            this.hotList = res.data.data
          }
        }
      });
    },
    getTrailerList () {
      uni.request({
        url: this.serverUrl + '/index/movie/hot?qq=lee83440456&type=trailer',
        method: "POST",
        success: (res) => {
          if (res.data.status === 200) {
            this.trailerList = res.data.data
          }
        }
      });
    },
    refreshCallback() {
      uni.stopPullDownRefresh()
      uni.hideLoading()
      uni.hideNavigationBarLoading()
    }
  },
  onPullDownRefresh() {
    uni.showNavigationBarLoading()
    uni.showLoading({
      mask: true
    })
    this.getGuessULikeList(this.refreshCallback)
  }
}
